<template>
  <v-card class="elevation-1 pa-3 login-card">
    <v-card-text>
      <div class="layout column align-center">
        <img src="/static/m.png" alt="SH" width="50" height="50" />
        <h1 class="flex my-4 primary--text">Salisbury Healthcare</h1>
      </div>
      <v-form>
        <v-text-field
          append-icon="person"
          name="login"
          label="Username"
          type="text"
          v-model="model.username"
        ></v-text-field>
        <v-text-field
          append-icon="lock"
          name="password"
          label="Password"
          id="password"
          type="password"
          v-model="model.password"
        ></v-text-field>
      </v-form>
    </v-card-text>
    <div v-if="valid">
       <v-alert :value="true" type="error">
        Please enter correct username and password.
      </v-alert>
    </div>
    <div class="login-btn">
      <!-- <v-btn icon>
        <v-icon color="blue">fa fa-facebook-square fa-lg</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon color="red">fa fa-google fa-lg</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon color="light-blue">fa fa-twitter fa-lg</v-icon>
      </v-btn>
      <v-spacer></v-spacer> -->
      <v-btn block color="primary" @click="login" :loading="loading">Login</v-btn>
    </div>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    loading: false,
    valid: false,
    model: {
      username: "",
      password: ""
    }
  }),

  methods: {
    login() {
      this.valid = false;
      console.log("This is login")
      this.loading = true;
      if(this.model.username == 'Supervisor' && this.model.password == 'Password'){
        localStorage.setItem('ROLE', 'Admin');
          setTimeout(() => {
            this.$router.push("/dashboard")
          }, 1000)
      }else if(this.model.username == 'Employee' && this.model.password == 'Password'){
        localStorage.setItem('ROLE', 'User');
          setTimeout(() => {
            this.$router.push("/userdashboard")
          }, 1000)
      }
      else{
        this.valid = true;
        this.model.username = '';
        this.model.password = '';
        this.loading = false;
    }
  }
  }
}
</script>
<style scoped lang="css"></style>
